@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;800&display=swap");
:root {
  /*     Common Colors     */
  --common-color: #00a0e9;
  /*     Font Size        */
  --fs-900: 9.375rem;
  --fs-800: 6.25rem;
  --fs-700: 3.5rem;
  --fs-600: 2rem;
  --fs-500: 1.75rem;
  --fs-400: 1.125rem;
  --fs-300: 1rem;
  --fs-200: 0.875rem;
  --fs-100: 0.65rem;
  /*     Font Color       */
  --clr-white: 0 0% 100%;
}

.theme--light {
  --background: white;
  --foreground: black;
  --clr-accent: 230 35% 7%;
  --clr-hover: 230 35% 7%;
  --clr-dark: 231 77% 90%;
  --clr-light: 0 0% 0%;
}

.theme--dark {
  --background: black;
  --foreground: white;
  --clr-accent: 231 77% 90%;
  --clr-hover: 230 35% 7%;
  --clr-dark: 230 35% 7%;
  --clr-light: 360 100% 100%;
  --clr-bg: #119191;
}

/*      Font Utility Size Class     */
.fs-900 {
  font-size: var(--fs-900);
}

.fs-800 {
  font-size: var(--fs-800);
}

.fs-700 {
  font-size: var(--fs-700);
}

.fs-600 {
  font-size: var(--fs-600);
}

.fs-500 {
  font-size: var(--fs-500);
}

.fs-400 {
  font-size: var(--fs-400);
}

.fs-300 {
  font-size: var(--fs-300);
}

.fs-200 {
  font-size: var(--fs-200);
}

.fs-100 {
  font-size: var(--fs-100);
}

/*      Font Utility Color Class    */
.text-accent {
  color: hsl(var(--clr-accent)/0.7);
}

.text-hover {
  color: hsl(var(--clr-accent));
}

body {
  font-family: "Inter", sans-serif;
}

/* ------------------- */
/* Reset               */
/* ------------------- */
/* Box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Reset margins */
body,
h1,
h2,
h3,
h4,
h5,
p,
figure,
picture {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-weight: 400;
}

/* set up the body */
body {
  font-size: var(--fs-400);
  color: hsl(var(--clr-white));
  background-color: hsl(var(--clr-dark));
  line-height: 1.5;
  min-height: 100vh;
}

/* make images easier to work with */
img,
picutre {
  max-width: 100%;
  display: block;
}

/* make form elements easier to work with */
input,
button,
textarea,
select {
  font: inherit;
}

/* remove animations for people who've turned them off */
@media (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.flow-gap {
  margin-top: var(--flow-space, 1rem) !important;
}

.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
     supported by Chrome, Edge, Opera and Firefox */
}

.uppercase {
  text-transform: uppercase;
}

.tagline {
  font-weight: 800;
  font-size: calc(2rem + 1vw);
  background: var(--bg-gradient, linear-gradient(to right, #00c6fb 0%, #005bea 100%));
  background-clip: text;
  background-position: left;
  background-size: 200%;
  color: transparent;
  letter-spacing: 0.01rem;
  word-spacing: 0.001rem;
  text-transform: uppercase;
  transition: 1s;
}

a {
  transition: 100ms ease-in-out;
}

.tagline:hover {
  background-clip: text;
  background-position: right;
  background-size: 200%;
}

body {
  background: var(--background);
  color: var(--foreground);
  font-size: 1rem;
  /*  Custom Scroll Bar YT Like    */
}
body .master-container {
  min-height: 80vh;
}
body .master-container .master-container__inner {
  display: grid;
  height: auto;
  width: calc(100% - 80px);
  margin: 2rem auto;
  grid-auto-rows: fit-content;
  gap: 2rem;
}
body::-webkit-scrollbar {
  width: 10px;
  left: -10px;
}
body::-webkit-scrollbar-thumb {
  background: #4A4A4A;
  border-radius: 1em;
}

.header {
  display: flex;
  height: 4rem;
  background: var(--background);
  color: hsl(var(--clr-accent)/0.7);
  font-size: var(--fs-400);
  font-weight: 600;
  justify-content: space-between;
  margin: 0 1rem;
}
.header .left-container {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.header .left-container .logo-wrapper {
  display: flex;
  font-size: var(--fs-500);
  font-weight: 800;
}
.header .left-container .logo-wrapper .logo {
  width: 3em;
  height: 3em;
}
.header .left-container .logo-wrapper .title {
  width: 12rem;
  height: 4rem;
}
.header .left-container .shimmer {
  text-align: center;
  color: rgba(255, 255, 255, 0.4);
  background: -webkit-gradient(linear, left top, right top, from(#222), to(#222), color-stop(0.5, #fff));
  background: gradient(linear, left top, right top, from(#222), to(#222), color-stop(0.5, #fff));
  background-size: 0px 100%;
  background-clip: text;
  animation-name: shimmer;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-color: #222;
}
@keyframes shimmer {
  0% {
    background-position: top left;
    background-size: 0px 100%;
  }
  10% {
    background-size: 40% 100%;
  }
  100% {
    background-position: right;
    background-size: 50px 100%;
  }
}
.header .right-container {
  display: flex;
}
.header .right-container .navigationBar {
  display: flex;
}
.header .right-container .navigationBar .navBtn__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
}
.header .right-container .navigationBar .navBtn__wrapper .navBtn__link {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
.header .right-container .navigationBar .navBtn__wrapper .navBtn__link:hover {
  color: hsl(var(--clr-accent));
}
.header .right-container .theme-switch {
  display: flex;
}
.header .right-container .theme-switch .switch-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.header .right-container .theme-switch .switch-wrapper button {
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
  opacity: 0.7;
  border-radius: 50%;
  position: relative;
}
.header .right-container .theme-switch .switch-wrapper button svg {
  width: 1.5rem;
  height: 1.5rem;
  fill: hsl(var(--clr-accent));
}
.header .right-container .theme-switch .switch-wrapper button svg .toggle-circle {
  transition: transform 250ms ease-out;
}
.header .right-container .theme-switch .switch-wrapper button::after {
  content: attr(aria-label);
  position: absolute;
  width: max-content;
  font-size: 0.8rem;
  color: var(--background);
  background: hsl(var(--clr-accent));
  right: -0.1rem;
  top: 120%;
  z-index: 1;
  margin: 0 auto;
  padding: 0.3rem 0.6rem;
  border-radius: 5px;
  opacity: 0;
  transform: scale(0);
  transform-origin: 80% 0%;
  pointer-events: none;
  transition: transform 100ms linear 100ms, opacity 100ms linear;
}
.header .right-container .theme-switch .switch-wrapper button:hover, .header .right-container .theme-switch .switch-wrapper button:focus-visible {
  opacity: 1;
  background: hsl(var(--clr-accent)/0.2);
}
.header .right-container .theme-switch .switch-wrapper button:hover::after, .header .right-container .theme-switch .switch-wrapper button:focus-visible::after {
  display: block;
  opacity: 1;
  transform: scale(1);
  transition: transform 70ms linear, opacity 100ms linear;
}
.header .right-container .theme-switch .switch-wrapper .wave {
  position: absolute;
  background: chartreuse;
  width: 1px;
  height: 1px;
  border-radius: 50%;
  z-index: -1;
  box-shadow: 0 0 0 0 #212121;
}

.theme--light svg .toggle-circle {
  transform: translateX(-15%);
}

.theme--light svg .toggle-sun {
  transform: rotate(0.5turn);
}

svg .toggle-sun {
  transform-origin: center center;
  transition: transform 500ms cubic-bezier(0.11, 0.14, 0.29, 1.32);
}

.notification__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: calc(100% - 2rem);
  margin: 0 auto;
  height: 40px;
  padding: 0 1rem;
  opacity: 0.9;
  color: var(--clr-light);
  box-shadow: 0 1.6px 3.6px 0 rgba(0, 0, 0, 0.13), 0 0.3px 0.9px 0 rgba(0, 0, 0, 0.11);
  border: 1px solid rgba(255, 255, 255, 0.35);
  background-color: #f5a623;
  border-radius: 5px;
  backdrop-filter: blur(15px);
  animation: drawerOpen 0.2s ease-in;
}
@keyframes drawerOpen {
  from {
    height: 0px;
    opacity: 0;
  }
  to {
    height: 40px;
    opacity: 1;
  }
}
.notification__wrapper .notification-button {
  opacity: 0.5;
}
.notification__wrapper .notification-button button {
  display: flex;
  cursor: pointer;
  justify-content: center;
  color: inherit;
  background: none;
  border: none;
}
.notification__wrapper .notification-button button svg {
  height: 1.5rem;
  width: 1.5rem;
}
.notification__wrapper .notification-button button::after {
  display: block;
  content: attr(aria-label);
  position: absolute;
  width: max-content;
  font-size: 0.8rem;
  color: var(--background);
  background: hsl(var(--clr-accent));
  top: 110%;
  margin: 0 auto;
  padding: 0.3rem 0.6rem;
  border-radius: 5px;
  opacity: 0;
  transform: scale(0);
  transform-origin: 80% 0;
  pointer-events: none;
  transition: transform 100ms linear 100ms, opacity 100ms linear;
}
.notification__wrapper .notification-button button:hover::after, .notification__wrapper .notification-button button:focus-visible::after {
  opacity: 1;
  transform: scale(1);
  transition: transform 70ms linear, opacity 100ms linear;
}
.notification__wrapper .notification-button:hover, .notification__wrapper .notification-button:focus-visible {
  opacity: 1;
}
.notification__wrapper .left-container {
  display: inline-flex;
}
.notification__wrapper .left-container .notification-count {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.5rem;
  background-color: black;
  color: white;
  font-size: 0.8rem;
  font-weight: 700;
  border-radius: 5px;
  backdrop-filter: blur(15px);
  padding: 1em;
  opacity: 0.9;
}
.notification__wrapper .notification-message {
  margin: 0 1rem;
  white-space: nowrap;
  overflow: hidden;
  color: var(--clr-dark);
}
.notification__wrapper .right-container {
  display: inline-flex;
}

.notification__wrapper--close {
  animation: drawerClose 0.2s ease-in;
  animation-fill-mode: forwards;
}
@keyframes drawerClose {
  from {
    height: 40px;
    opacity: 1;
  }
  to {
    height: 0px;
    opacity: 0;
  }
}

.master-view {
  position: relative;
  display: flex;
  height: fit-content;
  justify-content: center;
  align-items: center;
  padding: 0 40px;
}
.master-view .bg-gradient {
  position: absolute;
  z-index: -1;
  transform: scale(0.5);
}
.master-view .left-sec {
  display: flex;
  width: 40%;
  height: 100%;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  overflow: hidden;
}
.master-view .left-sec p {
  color: hsl(var(--clr-accent));
  text-align: justify;
  text-justify: inter-word;
}
.master-view .right-sec {
  width: 60%;
}

.subscribe-promotion {
  padding: 0 40px;
  color: hsl(var(--clr-dark));
  min-width: fit-content;
}
.subscribe-promotion .subscribe-promotion__wrapper {
  padding: 2rem;
  box-shadow: 0 1.6px 3.6px 0 rgba(0, 0, 0, 0.13), 0 0.3px 0.9px 0 rgba(0, 0, 0, 0.11);
  border: 1px solid rgba(255, 255, 255, 0.35);
  opacity: 0.4;
  background-color: hsl(var(--clr-light)/0.5);
  border-radius: 5px;
  background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/59615/bg--acrylic-light.png);
  backdrop-filter: blur(15px);
}
.subscribe-promotion .subscribe-promotion__wrapper .subscribe-promotion__title {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-size: calc(0.6rem + 2vw);
  text-transform: uppercase;
  letter-spacing: 0.01rem;
  word-spacing: 0.01rem;
  text-align: center;
  position: relative;
}
.subscribe-promotion .subscribe-promotion__wrapper .subscribe__form {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1.5rem 0;
  position: relative;
  opacity: 0.9;
}
.subscribe-promotion .subscribe-promotion__wrapper .subscribe__form .subscribe__form--email {
  height: 3rem;
  width: 18rem;
  outline: none;
  border: none;
  padding: 0.5rem;
  border-radius: 0.2em;
}
.subscribe-promotion .subscribe-promotion__wrapper .subscribe__form .subscribe__form--submit {
  height: 3rem;
  width: 6rem;
  margin-left: 0.5rem;
  outline: none;
  border: none;
  padding: 0.5rem;
  border-radius: 0.2em;
  cursor: pointer;
}
.subscribe-promotion .subscribe-promotion__wrapper .subscribe__form .subscribe__form--submit:hover {
  opacity: 1;
}
.subscribe-promotion .subscribe-promotion__wrapper .subscribe__form .alert-message {
  display: none;
  text-align: center;
  position: absolute;
  top: 60px;
  color: hsl(var(--clr-light));
  animation-duration: 4s;
  animation-name: fadeInOut;
  animation-timing-function: ease-in-out;
}
@keyframes fadeInOut {
  from {
    opacity: 0;
    display: none;
  }
  50% {
    opacity: 1;
    display: block;
  }
  to {
    opacity: 0;
    display: none;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
    display: none;
  }
  to {
    opacity: 1;
    display: block;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
    display: block;
  }
  to {
    opacity: 0;
    display: none;
  }
}
.subscribe-promotion .subscribe-promotion__wrapper::after {
  content: "";
  margin: 1em;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  background-image: url("./assets/images/send.svg");
  background-repeat: no-repeat;
  top: -10px;
  right: -20px;
  position: absolute;
  display: inline-block;
  z-index: -1;
}

.footer {
  border-top: 1px solid hsl(var(--clr-accent)/0.4);
  color: hsl(var(--clr-accent));
}
.footer .footer-wrapper {
  display: grid;
  margin: 1rem auto;
  width: calc(100% - 80px);
  grid-template-columns: 6rem auto;
}
.footer .footer-wrapper .left-sec {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.footer .footer-wrapper .left-sec img {
  height: 5rem;
  width: 5rem;
  display: inline-block;
}
.footer .footer-wrapper .left-sec p {
  margin: 0.5rem 0;
  font-size: var(--fs-100);
  font-weight: 300;
}
.footer .footer-wrapper .right-sec {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-auto-flow: row;
  margin-left: 2rem;
}
.footer .footer-wrapper .right-sec .footer-links {
  grid-row: 1/3;
  display: flex;
  flex-wrap: wrap;
}
.footer .footer-wrapper .right-sec .footer-links .footer-link {
  display: block;
  margin-right: 0.8rem;
  margin-left: 0.8rem;
  color: hsl(var(--clr-accent)/0.7);
}
.footer .footer-wrapper .right-sec .footer-links .footer-link a {
  font-size: var(--fs-400);
  text-decoration: none;
  color: inherit;
}
.footer .footer-wrapper .right-sec .footer-links .footer-link a:hover {
  color: hsl(var(--clr-accent));
}
.footer .footer-wrapper .right-sec .footer-miscs {
  grid-row: 4/5;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: var(--fs-200);
}
.footer .footer-wrapper .right-sec .footer-miscs .footer-misc {
  display: inline-block;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.gallery {
  display: flex;
  width: 100%;
  height: 48rem;
  padding: 0 40px;
  flex-direction: column;
  align-items: center;
}
.gallery .gallery__wrapper {
  width: 100%;
  height: 100%;
  overflow: auto;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-auto-columns: 16rem;
  grid-auto-flow: column;
  grid-gap: 1rem;
}
.gallery .gallery__wrapper .gallery__item {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  border-radius: 1rem;
  z-index: 0;
}
.gallery .gallery__wrapper .gallery__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: inherit;
}
.gallery .gallery__wrapper .gallery__item .gallery__item-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  bottom: 0;
  z-index: 2;
}
.gallery .gallery__wrapper .gallery__item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: inherit;
  background: linear-gradient(to top, #5b6281 0%, rgba(87, 92, 117, 0.7) 40%, rgba(87, 92, 117, 0.2) 100%);
  opacity: 0;
  transition: ease-in-out 1s;
  z-index: 1;
}
.gallery .gallery__wrapper .gallery__item:hover::after {
  content: "";
  position: absolute;
  border-radius: inherit;
  opacity: 1;
  background: linear-gradient(to top, #5b6281 0%, rgba(87, 92, 117, 0.7) 40%, rgba(87, 92, 117, 0.2) 100%);
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 100%;
}

.social {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 40px;
  overflow: hidden;
}
.social .bg-gradient {
  position: absolute;
  left: -10%;
  z-index: -1;
  transform: scale(0.6);
}
.social .social-wrapper {
  display: flex;
  margin: 0 1em;
  width: 100%;
  overflow: hidden;
}
.social .social-wrapper .social-item {
  padding: 2rem;
  box-shadow: 0 1.6px 3.6px 0 rgba(0, 0, 0, 0.13), 0 0.3px 0.9px 0 rgba(0, 0, 0, 0.11);
  border: 1px solid rgba(255, 255, 255, 0.35);
  opacity: 0.4;
  background-color: hsl(var(--clr-light)/0.5);
  border-radius: 5px;
  background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/59615/bg--acrylic-light.png);
  backdrop-filter: blur(15px);
  display: flex;
  width: 100%;
  height: 20rem;
  min-width: fit-content;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: width ease-in 100ms, opacity ease-in 100ms;
}
.social .social-wrapper .social-item svg {
  fill: var(--foreground);
  height: 100px;
  width: 100px;
}
.social .social-wrapper .discord:hover {
  background-image: url(https://github.com/hash-define-organization/website-update/raw/main/src/assets/images/social/discord.jpg);
  background-size: cover;
  width: 120%;
  opacity: 1;
}
.social .social-wrapper .whatsapp:hover {
  background-image: url(https://github.com/hash-define-organization/website-update/raw/main/src/assets/images/social/whatsapp.jpg);
  background-size: cover;
  width: 120%;
  filter: hue-rotate(-15deg);
  opacity: 1;
}
.social .social-wrapper .linkedin:hover {
  background-image: url(https://github.com/hash-define-organization/website-update/raw/main/src/assets/images/social/linkedin.jpg);
  background-size: cover;
  width: 120%;
  opacity: 1;
}
.social .social-wrapper .social-item:not(:last-child) {
  margin-right: 1rem;
}